




















import Vue from "vue";
import { Component, Prop, Watch } from "vue-property-decorator";
import Button from "@/components/Button.vue";

@Component({
  components: {
    Button,
  },
})
export default class Label extends Vue {
  get tagList() {
    this.$store.commit("fetchTag");
    return this.$store.state.tagList;
  }
}
